  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = () => {
      setInputImage(reader.result);
    };
    reader.readAsDataURL(file);
  };
  
  // Function to handle image cropping
  const handleImageCrop = () => {
    // Create a new image element
    const img = new Image();
    
    // Set the src of the image element to the input image
    //img.src = inputImage;
    img.src=canvasRef.current.getDataURL();
    img.onload = () => {
      // Perform image cropping here
    
    // Create a canvas element
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');

    // Set the width and height of the canvas to the image width and height
    canvas.width = img.width;
    canvas.height = img.height;

    // Draw the image on the canvas
    ctx.drawImage(img, 0, 0);

    // Get the pixel data from the canvas
    const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);

    // Find the edges of the non-transparent part of the image
    let top = imageData.height,
        bottom = 0,
        left = imageData.width,
        right = 0;

    for (let y = 0; y < imageData.height; y++) {
      for (let x = 0; x < imageData.width; x++) {
        const index = (y * imageData.width + x) * 4;
        const alpha = imageData.data[index + 3];
        if (alpha > 0) {
          if (y < top) top = y;
          if (y > bottom) bottom = y;
          if (x < left) left = x;
          if (x > right) right = x;
        }
      }
    }

    // Crop the image based on the edges
    const croppedCanvas = document.createElement('canvas');
    const croppedCtx = croppedCanvas.getContext('2d');
    const croppedWidth = right - left + 1;
    const croppedHeight = bottom - top + 1;
    croppedCanvas.width = croppedWidth;
    croppedCanvas.height = croppedHeight;
    croppedCtx.drawImage(canvas, left, top, croppedWidth, croppedHeight, 0, 0, croppedWidth, croppedHeight);
    setCroppedImage(croppedCanvas.toDataURL());
    // const link = document.createElement('a');
    const now = new Date();
const options = {
  //weekday: 'long',
  year: 'numeric',
  month: 'long',
  day: 'numeric',
  hour: 'numeric',
  minute: 'numeric',
  second: 'numeric',
  timeZone: Intl.DateTimeFormat().resolvedOptions().timeZone,
  //timeZoneName: 'short'
};
const timestamp = now.toLocaleString(Intl.DateTimeFormat().resolvedOptions().locale, options);
    //const timestamp = new Date().toISOString().replace(/:/g, '-');
    // link.download = `${timestamp}.png`;
    createfile(croppedCanvas.toDataURL(),timestamp)
    // link.href = croppedCanvas.toDataURL();
    
   // link.click();
  };
  };
